// 90-2)  state 
let defaultState =
  [
    // { id: 0, name: 'TOTORO  0', quan: 1, price: 10000 },
    // { id: 1, name: 'TOTORO  1', quan: 2, price: 12000 },
    // { id: 2, name: 'TOTORO  2', quan: 3, price: 11000 },
    // { id: 3, name: 'TOTORO  3', quan: 4, price: 9000 }
  ];

// 90-1) redux    ,    . data  '=' .
function reducer(state = defaultState, actions) {
  // 101-1)  

  let copy = [...state]; // deep copy

  switch (actions.type) {
    case '':
      console.log("reducer:: actions.payload.id = %d", actions.payload.id);
      let idx = actions.payload.id;
      console.log("reducer:: idx = %d", idx);

      let found = false;
      if (copy.length > 0) {
        for (let i = 0; i < copy.length; i++) {
          if (actions.payload.id === copy[i].id) {
            // id    +1 
            copy[i].quan++;
            found = true;
            break;
          }
        }

        //  id   
        if (found === false) {
          copy.push(actions.payload);
        }
      }
      else {
        copy.push(actions.payload);
      }
      return copy;
    case '':
      copy[actions.payload.id].quan++;
      return copy;
    case '':
      let quan = copy[actions.payload.id].quan;
      if (quan >= 1) {
        copy[actions.payload.id].quan--;
      }
      return copy;
    default:
      //  state  .
      return state;
  }
}

// 100-1)     ,  
let alertAdmob = true;

function reducer2(state = alertAdmob, actions) {
  if (actions.type === '') {
    // 100-4) state  
    state = false;
    return state;
  } else {
    return state;
  }
}

// 90-3) state  ,    state props .
// 100-2) reducer  
//let store = createStore(reducer);
let store = createStore(combineReducers({ reducer, reducer2 }));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      {/* 80-4) store   . */}
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

