import React from "react";
import { Table } from "react-bootstrap";
// 80-7) connect import   
// 104-3) import useSelector
// 104-4) import useDispatch
import { connect, useDispatch, useSelector } from 'react-redux';
import '../Detail.scss'

//   
function Cart(props) {

    // 104-2)  ,   , redux    state 
    let state = useSelector((state) => state);
    console.log("useSelector, state : %s", state);

    // 104-3)  ,     . props .
    let dispatch = useDispatch();

    return (
        <div>

            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>

                    {
                        // props.state.map((stuff1, idx) => {
                        state.reducer.map((stuff1, idx) => {
                            console.log("stuff1[%d]: %s", idx, stuff1);
                            //    
                            return (
                                <tr key={idx}>
                                    <td>{stuff1.name}</td>
                                    <td>{stuff1.quan}</td>
                                    <td><button onClick={() => {
                                        //  dispatch({type:'', payload: })
                                        // 104-5) useDispatch()  props     .
                                        // props.dispatch({ type: '', payload: { name: 'A' }, id: idx })
                                        dispatch({
                                            type: '', payload: stuff1
                                        })

                                    }}>+</button>

                                        <button onClick={() => {
                                            //  
                                            // 104-5) useDispatch()  props     .
                                            // props.dispatch({ type: '', id: idx })
                                            dispatch({
                                                type: '', payload: stuff1
                                            })

                                        }}>-</button></td>
                                    <td>{stuff1.price}</td>
                                </tr>
                            );

                        })
                    }

                </tbody>
            </Table>
            {
                props.alertSale === true
                    ?
                    <div className="alert-sold-out-yellow">
                        <p>   20%</p>
                        <button onClick={() => {
                            // 104-5) useDispatch()  props     .
                            // props.dispatch({ type: '' })
                            dispatch({ type: '' })
                        }}></button>
                    </div>
                    : null
            }


        </div >
    )
}

